/**
# TypedHistoryBuffer

A TypedHistoryBuffer is a HistoryBuffer with information about the data type
of the values being pushed to it.

The data type is used exclusively for optimizing the push of new data into the buffer operation.

Example:
```javascript
var hb1 = new TypedHistoryBuffer(1024);

hb1.setNIType(NITypes.DOUBLE.makeArray());

hb1.pushTypedData([1, 2]);
hb1.pushTypedData([3, 4, 5]);

console.log(hb1.toArray()); //[1, 2, 3, 4, 5]
```

TypedHistoryBuffer with width = 2:
```javascript
var hb1 = new TypedHistoryBuffer(1024, 2);

hb1.setNIType(NITypes.DOUBLE.makeArray(2));

hb1.pushTypedData([[1, 11], [2, 22]]);
hb1.pushTypedData([[3, 33], [4, 44], [5, 55]]);

console.log(hb1.toArray()); //[[1, 11], [2, 22], [3, 33], [4, 44], [5, 55]]
```

*/

/** ## TypedHistoryBuffer methods*/
(function () {
    'use strict';

    var DataPipeline = NationalInstruments.HtmlVI.DataPipeline;
    var DataTypesEnum = NationalInstruments.HtmlVI.DataPipeline.DataTypesEnum;

    /** **TypedHistoryBuffer(capacity, width)** - the History buffer constructor creates
    a new history buffer with the specified capacity (default: 1024) and width (default: 1).
    The data type is set to unkown. */
    NationalInstruments.HtmlVI.DataPipeline.TypedHistoryBuffer = function () {
        HistoryBuffer.apply(this, arguments);
        this.niType = null;
        this.valueType = DataTypesEnum.unknown;
    }

    var TypedHistoryBuffer = NationalInstruments.HtmlVI.DataPipeline.TypedHistoryBuffer;

    TypedHistoryBuffer.prototype = new HistoryBuffer(arguments);

    /** **setNItype(niType)** - sets the data type of the values being pushed.
        Supported data types:
        * any numeric type
        * one dimension numeric arrays
        * two dimensions numeric arrays
        * clusters of numerics
        * array of clusters of numerics
     */
    TypedHistoryBuffer.prototype.setNIType = function(niType) {
        var width;
        this.niType = niType;
        this.valueType = DataPipeline.inferDataTypeFromNIType(this.niType);
        this.clear();

        switch (this.valueType) {
            case DataTypesEnum.numeric:
            case DataTypesEnum.oneDimensionNumericArray:
                this.setType('numeric');
                this.setWidth(1);
                break;
            case DataTypesEnum.clusterOfNumerics:
                this.setType('numeric');
                width = this.niType.getFields().length;

                if (width === 0) {
                    return;
                }

                this.setWidth(width);
                break;
            case DataTypesEnum.arrayOfClusterOfNumerics:
            case DataTypesEnum.XYArrayOfClusters:
                width = this.niType.getSubtype().getFields().length

                if (width === 0) {
                    return;
                }

                this.setWidth(width);
                break;
            case DataTypesEnum.niAnalogWaveform:
                this.setType('analogWaveform');
                this.setWidth(1);
                break;
            case DataTypesEnum.niAnalogWaveformArray:
                this.setType('analogWaveform');
                this.setWidth(1);
                break;
        }
    }

    /** **pushTypedData(value)** - pushes a new value to the history buffer */
    TypedHistoryBuffer.prototype.pushTypedData = function(value) {
        var i, j, row, keys, width;
        switch (this.valueType) {
            case DataTypesEnum.numeric:
                this.push(value);
                break;
            case DataTypesEnum.oneDimensionNumericArray:
                this.appendArray(value);
                break;
            case DataTypesEnum.niAnalogWaveform:
                this.push(value);
                break;
            case DataTypesEnum.niAnalogWaveformArray:
                width = value.length;

                if (width === 0) {
                    // empty array, nothing to push, can't set the width to 0 so don't do anything
                    return;
                }
                this.setWidth(value.length);
                this.push(value);
                break;
            case DataTypesEnum.twoDimensionsNumericArray:
                width = value[0].length;

                if (width === 0) {
                    // empty array, nothing to push, can't set the width to 0 so don't do anything
                    return;
                }

                this.setWidth(width);
                for (i = 0; i < value.length; i++) {
                    this.push(value[i]);
                }
                break;
            case DataTypesEnum.clusterOfNumerics:
                row = [];
                keys = Object.keys(value);
                for (i = 0; i < keys.length; i++) {
                    row[i] = value[keys[i]];
                }
                this.push(row);
                break;
            case DataTypesEnum.arrayOfClusterOfNumerics:
            case DataTypesEnum.XYArrayOfClusters:
                for (j = 0; j < value.length; j++) {
                    row = [];
                    keys = Object.keys(value[j]);
                    for (i = 0; i < keys.length; i++) {
                        row[i] = value[j][keys[i]];
                    }
                    this.push(row);
                }
                break;
        }
    }
}());
