/*jslint nomen: true, devel:true*/
//****************************************
// Cartesian Axis Custom Element
// DOM Registration: HTMLNICartesianAxis
// National Instruments Copyright 2017
//****************************************

/**
## ni-cartesian-axis

> Add axes to a graph.

```html
<ni-cartesian-graph>
  <ni-cartesian-axis show grid-lines show-label label="Axis 1" axis-position='bottom'>
  </ni-cartesian-axis>
  <ni-cartesian-axis show grid-lines show-label label="Axis 2" axis-position='top'>
  </ni-cartesian-axis>
  <ni-cartesian-axis show grid-lines show-label label="Axis 3" axis-position='left'>
  </ni-cartesian-axis>
</ni-cartesian-graph>
```
Multiple vertical and horizontal axes can be added to a graph and configured.

Axes can be placed to the right, left and on top or bottom of a graph, can use a
logarithmic scale if desired and can show time. A relative time format is supported
along with the absolute time format.

The minimum and maximum value of an axis can be specified. Autoscaling according
to the data ranges dispayed is also possible.
*/

(function() {
    'use strict';

    var NINumericFormatters = window.NINumericFormatters;

    var escapeHtmlString = function(string) {
        var entityMap = {
            '&': '&amp;',
            '<': '&lt;',
            '>': '&gt;',
            '"': '&quot;',
            "'": '&apos;',
            '/': '&#x2F;',
            '`': '&#x60;'
        };

        return String(string).replace(/[&<>"'`/]/g, function fromEntityMap (s) {
            return entityMap[s];
        });
    };

    var isTimeAxis = function(format) {
        return (format.indexOf('LVTime') === 0) || (format.indexOf('LVRelativeTime') === 0);
    };

    var toFlotTimeFormat = function(format) {
        if (format.indexOf('LVTime') === 0) {
            return '%A';
        }

        if (format.indexOf('LVRelativeTime') === 0) {
            return '%r';
        }

        return null;
    };

    var siTickFormatter = function (num, axis, precision, plot) {
        var decimals = axis.tickDecimals,
            log10AxisMin, croppedMin, croppedMax;

        if (axis.tickDecimals !== axis.options.tickDecimals) {
            // we recompute the precision given by flot by considering another axis range.
            // For example, if axis.min=1000 and axis.max=1200 we recompute the numbers of ticks for min=1 and max=1.2 which gives us 2 decimals needed instead of 0
            croppedMin = computeCroppedValue(axis.min);
            log10AxisMin = axis.min !== 0 ? Math.floor(Math.log(Math.abs(axis.min)) * Math.LOG10E) : 0;
            croppedMax = computeCroppedValue(axis.max, log10AxisMin);

            decimals = Math.abs(plot.computeValuePrecision(croppedMin, croppedMax, axis.direction, axis.options.ticks));
        }

        if (precision) {
            var precisionDecimals = Math.abs(precision) <= 3 ? Math.abs(precision) + 1 : reduceNumberOfDecimals(num, precision, axis.max, axis.min);
            if (precisionDecimals > decimals) {
                //the precision of endpoints should not be smaller then the one used for major ticks
                return NINumericFormatters.prototype.toSiNotation(num, precisionDecimals);
            }
        }

        return NINumericFormatters.prototype.toSiNotation(num, decimals);
    };

    //heuristic for recomputing the number of decimals to [|log10(|num|) + tickDecimals|]
    //where tickDecimals is compunted based on axis.max-axis.min
    var reduceNumberOfDecimals = function(num, tickDecimals, max, min) {
        var log10Max = Math.log(Math.abs(max)) * Math.LOG10E,
            log10Min = min !== 0 ? Math.log(Math.abs(min)) * Math.LOG10E : 0,
            orderSizeDif = Math.floor(Math.abs(log10Max - log10Min)),
            log10Value = num !== 0 ? Math.log(Math.abs(num)) * Math.LOG10E : 0,
            //the precision computed in flot will be positive for small interval and negative otherwise
            decimals = Math.abs(log10Value + tickDecimals);

        //the range of axis is big so we need to reduce the no of decimals
        if (orderSizeDif > 3) {
            return Math.ceil(decimals * 3 / orderSizeDif);
        }

        return Math.ceil(decimals);
    };

    var computeCroppedValue = function(val, roundingPower) {
        var log10Value = val !== 0 ? Math.floor(Math.log(Math.abs(val)) * Math.LOG10E) : 0,
            exp = Math.pow(10, log10Value),
            croppedVal = val;

        if (roundingPower > 0) {
            croppedVal /= Math.pow(10, Math.floor(roundingPower / 3) * 3);
        } else croppedVal *= Math.pow(10, log10Value % 3) / exp;

        return croppedVal;
    }

    var roundUsing = function(roundFunction, number, precisionDigits) {
        var precision = Math.pow(10, precisionDigits);
        var tempNumber = number * precision;
        tempNumber = roundFunction(tempNumber);

        return tempNumber / precision;
    };

    var getSiTickFormatter = function(options) {
        if (options.indexOf('G') === -1 && options.indexOf('N') === -1) {
            if (options.indexOf('#') !== -1 && options.lastIndexOf('#') === options.length - 1) {
                return function(num) {
                    var decimals = options.lastIndexOf('#') - options.indexOf('#') + 1;
                    return NINumericFormatters.prototype.toSiNotation(num, decimals);
                }
            }

            if (options === '0') {
                return function(num) {
                    return NINumericFormatters.prototype.toSiNotation(num, 0);
                }
            }
            //use default si tick formatter for auto-formatting
            return siTickFormatter;
        }

        return function(num) {
            var decimals = parseInt(options.slice(1)),
                siValue = NINumericFormatters.prototype.toSiNotation(num, decimals),
                value, extension;

            if (options.indexOf('G') === 0) {
                if (isNaN(Number(siValue))) {
                    value = Number(siValue.slice(0, siValue.length - 1));
                    extension = siValue.slice(siValue.length - 1);
                } else {
                    value = Number(siValue);
                    extension = ''
                }
                return NINumericFormatters.prototype.toDecPrecisionNotation(value, Math.min(decimals, 21)) + extension;
            } else {
                return siValue;
            }
        };
    }

    // Nx or Gx or 0.##...#
    // where N and 0.##...# is for Fixed Notation and G is for Significant Digits Precision
    // x is a natural number representing the number of decimals/digits
    // in case of 0.##...#, the number of # represents the number of decimals
    var getDecTickFormatter = function(options) {
        if (options.indexOf('G') === -1 && options.indexOf('N') === -1) {
            if (options.lastIndexOf('#') === options.length - 1) {
                return function(num) {
                    var decimals = options.lastIndexOf('#') - options.indexOf('#') + 1,
                        value = roundUsing(Math.floor, num, decimals);
                    return NINumericFormatters.prototype.toDecFixedNotation(value, decimals);
                }
            }

            if (options === '0') {
                return function(num) {
                    return NINumericFormatters.prototype.toDecFixedNotation(num, 0);
                }
            }
            //use flot default tick formatter for auto-formatting
            return null;
        }

        return function(num) {
            var len = parseInt(options.slice(1));
            var value = num;

            if (options.indexOf('G') === 0) {
                return NINumericFormatters.prototype.toDecPrecisionNotation(value, Math.min(len, 21));
            } else {
                value = roundUsing(Math.floor, num, len);
                return NINumericFormatters.prototype.toDecFixedNotation(value, len);
            }
        };
    };

    var chooseTickFormatter = function(format, showTickLabels) {
        var colonMarkIndex;
        if (format.indexOf('LVSI') === 0) {
            colonMarkIndex = 5;
            var formatSubstring = format.slice(colonMarkIndex);
            return getSiTickFormatter(formatSubstring);
        }
        // 'LVRelativeSeconds:Gx' or LVRelativeSeconds:0.##...#
        // Ex: 'LVRelativeSeconds:N4'
        // where N represents the Fixed Notation,
        // G represents the Significant Digits Precision and
        // x is a number representing the number of decimals/digits
        if (format.indexOf('LVRelativeSeconds') === 0) {
            colonMarkIndex = 18;
            var decFormatTypeSubstring = format.slice(colonMarkIndex);
            return getDecTickFormatter(decFormatTypeSubstring);
        }

        // use flot tick formatter
        return null;
    };

    var getDefaultAutoScale = function (axis, scaletype) {
        if (scaletype === "auto") {
            if (axis._parentGraph.nodeName === "NI-INTENSITY-GRAPH") {
                return "exact";
            } else {
                if (axis._parentGraph.nodeName === "NI-CHART") {
                    return (axis.axisPosition === "bottom" || axis.axisPosition === "top")
                        ? "sliding-window"
                        : "loose";
                }

                return (axis.axisPosition === "bottom" || axis.axisPosition === "top")
                    ? "exact"
                    : "loose";
            }
        }

        return scaletype;
    };

    var getWindowSize = function (axis) {
        if (axis._parentGraph.nodeName === "NI-CHART" && (axis.axisPosition === "bottom" || axis.axisPosition === "top")) {
            var windowSize = (axis._defaultMax - axis._defaultMin);
            return (windowSize > 1 ? windowSize : 1024);
        }

        return undefined;
    }

    class CartesianAxis extends window.JQX.BaseElement {
        /**### ni-cartesian-axis properties*/
        static get properties() {
            const props = Object.assign({}, super.properties, {

                /** **axis-ref** - an axis id used to bind plots to the axes. Default: ''*/
                'axisRef': {
                    value: '',
                    type: 'string'
                },

                /** **show** (boolean) - if true the axis will be visible. Default: false*/
                'show': {
                    value: false,
                    type: 'boolean'
                },

                /** **label** (string) - the name of the axis. Default: '' */
                'label': {
                    value: '',
                    type: 'string'
                },

                /**
                **axis-position** (string) - the position of the axis. Default: 'left'
                 Accepted values: 'top', 'bottom', 'left' and 'right'
                */
                'axisPosition': {
                    value: 'left',
                    type: 'string'
                },

                /** **show-label** (boolean) - if true the axis label will be visible.
                Default: false */
                'showLabel': {
                    value: false,
                    type: 'boolean'
                },

                /** **minimum** (number) - the minimum axis value. Default: 0*/
                'minimum': {
                    value: 0,
                    type: 'number'
                },

                /** **maximum** (number) - the maximum axis value. Default: 1*/
                'maximum': {
                    value: 1,
                    type: 'number'
                },

                /** **auto-scale** (string) - if 'exact' or 'loose' the axis will adapt to the data
                ranges of the plots assigned to that axis. if 'none', the axis will adapt to the
                minimum and maximum options. For 'growexact' and 'growloose' the axis will adapt only when
                the new data value is bigger than the old one. Default: 'auto'*/
                'autoScale': {
                    value: 'auto',
                    type: 'string'
                },

                /** **log-scale** (boolean) - if true the axis will be a log axis. Default: false*/
                'logScale': {
                    value: false,
                    type: 'boolean'
                },

                /**
                **format** (string) - the format of the tick labels. Default: ''
                Possible values: 'LVSI, 'LVTime', 'LVRelativeTime', 'LVRelativeSeconds'.
                Examples:

                |format|value(s)|formatted value(s)|
                |------|----:|--------------:|
                |_not specified_|0.1234567|0.1234567|
                |LVSI|0.1234567|123.4567m|
                |LVSI:0|0.1234567|123m|
                |LVSI:0.###|0.1234567|123.457m|
                |LVSI:G3|0.1234567|123m|
                |LVSI:G3|1.234567|1.23|
                |LVSI:N3|0.1234567|123.457m|
                |LVTime|0|12:00:00 AM 1/1/0001|
                |LVTime|300|12:05:00 AM 1/1/0001|
                |LVRelativeTime|0, 300, 600|00:00:00, 00:05:00, 00:10:00|
                |LVRelativeTime|300, 600, 900|00:00:00, 00:05:00, 00:10:00|
                |LVRelativeSeconds:300|300|300.0|
                |LVRelativeSeconds:0.###|0.12345678|0.123|
                |LVRelativeSeconds:G3|0.12345678|0.123|
                |LVRelativeSeconds:G3|1.2345678|1.23|
                |LVRelativeSeconds:N3|1234.5678|1234.567|

                The actual starting time corresponding to the 0 value for the 'LVTime' format
                may vary depending on the time zone.

                The first value of each axis using the 'LVRelativeTime' format is considered
                the starting point for that axis and it will always be formatted as 00:00:00.

                The actual number of decimals may be larger than specified for the endpoints.
                When the range of an axis using the 'LVRelativeTime' format is smaller than 1
                minute then the milliseconds will be displayed as well.

                Other items which are displaying values, like the cursors or cursor legend,
                will use this format as well.
                */
                'format': {
                    value: '',
                    type: 'string'
                },

                /** **show-tick-labels** (string) - which of the tick labels are visible.
                Possible values: 'none', 'endpoints', 'major' and 'all'. Default: 'all'.
                For 'endpoints' only the minimum and the maximum values will be visible
                at the edges of the axis. To make some intermediary values visible use
                'major', and 'all' to show all of them.*/
                'showTickLabels': {
                    value: 'all',
                    type: 'string'
                },

                /** **grid-lines** (boolean) - if true the grid lines for the axis will
                be visible. Default: false*/
                'gridLines': {
                    value: false,
                    type: 'boolean'
                },

                /** **show-ticks** (boolean) - if true the tick lines for the axis will
                be visible. A tick line is a short line which connects the tick label with
                the axis. Default: false*/
                'showTicks': {
                    value: false,
                    type: 'boolean'
                },

                /** **show-minor-ticks** (boolean) - if true the minor tick lines for the axis
                will be visible. Minor tick lines are short lines (half of the tick line
                length) which subdivide the interval between tick lines in five intervals.
                Default: false*/
                'showMinorTicks': {
                    value: false,
                    type: 'boolean'
                },

                /** **pan** (string) - what type of panning is allowed for the axis.
                Possible values: 'none', 'axis', 'plot', 'all'. Default: 'all'.
                For 'axis' panning can be done only when the mouse/touch is over the axis,
                while for 'plot', the panning is done only if the entire plot is panned. */
                pan: {
                    value: 'all',
                    type: 'string'
                },

                /** **zoom** (string) - what type of panning is allowed for the axis.
                Possible values: 'none', 'axis', 'plot', 'all'. Default: 'all'.
                For 'axis' zooming can be done only when the mouse/touch is over the axis,
                while for 'plot', the zooming is done only if the entire plot is zommed. */
                zoom: {
                    value: 'all',
                    type: 'string'
                }
            });

            return props;
        };

        created() {
            super.created();

            this._defaultMin = this.minimum;
            this._defaultMax = this.maximum;
            this._parentGraph = undefined;
        };

        sendEventToParentGraph(name) {
            var eventConfig;

            if (this._parentGraph !== undefined) {
                eventConfig = {
                    bubbles: false,
                    cancelable: true,
                    detail: {
                        element: this
                    }
                };

                this._parentGraph.dispatchEvent(new CustomEvent(name, eventConfig));
            }
        };

        attached() {
            super.attached();

            if (this.parentElement instanceof NationalInstruments.HtmlVI.Elements.CartesianGraphBase && this.parentElement.isReady) {
                this.attachToGraph(this.parentElement);
            } else {
                this._parentGraph = undefined;
            }
            this._defaultMin = this.minimum;
            this._defaultMax = this.maximum;
        };

        attachToGraph(graph) {
            if (!this._parentGraph) {
                this._parentGraph = graph;
                this.sendEventToParentGraph('ni-cartesian-axis-attached');
            }
        };

        propertyChangedHandler(propertyName, oldValue, newValue) {
            super.propertyChangedHandler(propertyName, oldValue, newValue);

            this.sendEventToParentGraph('ni-axis-changed');
        };

        detached() {
            super.detached();

            this.sendEventToParentGraph('ni-cartesian-axis-detached');
            this._parentGraph = undefined;
        };

        setFont(fontSize, fontFamily, fontWeight, fontStyle, textDecoration) {
            this.style.fontSize = fontSize;
            this.style.fontFamily = fontFamily;
            this.style.fontWeight = fontWeight;
            this.style.fontStyle = fontStyle;
            this.style.textDecoration = textDecoration;

            this.sendEventToParentGraph('ni-axis-changed');
        };

        // here we transform the configuration from the one used by the custom element to the one understood by Flot
        getViewConfig() {
            var currStyle = window.getComputedStyle(this),
                grow = (/grow/).test(this.autoScale),
                scaletype = grow ? this.autoScale.replace(/grow/, '') : this.autoScale;

            var flotConfig = {
                axisLabel: this.showLabel ? escapeHtmlString(this.label) : undefined,
                show: this.show,
                position: this.axisPosition,
                min: this.minimum,
                max: this.maximum,
                autoscale: getDefaultAutoScale(this, scaletype),
                windowSize: getWindowSize(this),
                growOnly: grow,
                mode: this.logScale ? 'log' : 'linear',
                element: this,
                format: isTimeAxis(this.format) ? 'time' : null,
                timeformat: toFlotTimeFormat(this.format),
                tickFormatter: chooseTickFormatter(this.format, this.showTickLabels),
                showTickLabels: this.showTickLabels,
                gridLines: this.gridLines,
                showTicks: this.showTicks,
                showMinorTicks: this.showMinorTicks,
                color: currStyle.color,
                offset: this._navigationOffset || {below: 0, above: 0},
                font: {
                    size: parseFloat(currStyle.fontSize),
                    style: currStyle.fontStyle,
                    weight: currStyle.fontWeight,
                    family: currStyle.fontFamily,
                    color: 'inherit'
                },
                axisZoom: this.zoom === 'axis' || this.zoom === 'all',
                plotZoom: this.zoom === 'plot' || this.zoom === 'all',
                axisPan: this.pan === 'axis' || this.pan === 'all',
                plotPan: this.pan === 'plot' || this.pan === 'all'
            };

            return flotConfig;
        };

        // get the flot axis coresponding to this element
        getFlotAxis() {
            var graph = this._parentGraph.graph;
            var axes = graph.getAxes();
            var that = this;

            var key = Object.keys(axes).filter(function (axis) {
                return axes[axis].options.element === that;
            })[0];

            return axes[key];
        };

        // set the axis min and max to the data min and max
        scaleOnce() {
            var axis = this.getFlotAxis(),
                flot = this._parentGraph.graph,
                allSeries = flot.getData(),
                ranges = allSeries
                    .filter(function(series) {
                        return series.xaxis === axis || series.yaxis === axis;
                    })
                    .map(function(series) {
                        return flot.computeRangeForDataSeries(series, true);
                    }),
                defaultRange = {
                    xmin: Number.POSITIVE_INFINITY,
                    ymin: Number.POSITIVE_INFINITY,
                    xmax: Number.NEGATIVE_INFINITY,
                    ymax: Number.NEGATIVE_INFINITY
                },
                range = ranges.reduce(function(p, c) {
                    return {
                        xmin: Math.min(p.xmin, c.xmin),
                        ymin: Math.min(p.ymin, c.ymin),
                        xmax: Math.max(p.xmax, c.xmax),
                        ymax: Math.max(p.ymax, c.ymax)
                    };
                }, defaultRange);

            this._navigationOffset = axis.options.offset = {below: 0, above: 0};

            var min = axis.direction === 'x' ? range.xmin : range.ymin;
            var max = axis.direction === 'x' ? range.xmax : range.ymax;

            this.minimum = min !== Number.POSITIVE_INFINITY ? min : this._defaultMin;
            this.maximum = max !== Number.NEGATIVE_INFINITY ? max : this._defaultMax;

            this.sendEventToParentGraph('ni-axis-changed');
        };

        // called after an user interacts with the plot so the attributes of the element are in sync with the plot
        syncWithFlot() {
            var axis = this.getFlotAxis();

            if (typeof axis.options.min === 'number') {
                this.minimum = axis.options.min;
                this.maximum = axis.options.max;
            }

            if (typeof axis.options.offset === 'object') {
                this._navigationOffset = {
                    below: axis.options.offset.below,
                    above: axis.options.offset.above
                };
            }

            if (this._parentGraph !== undefined) {
                this._parentGraph.notifyScaleLegend('ni-axis-changed', this);
            }
        };

        template() {
            return '';
        }
    };

    JQX('ni-cartesian-axis', CartesianAxis);
}());
