//****************************************
// Cartesian Graph Base Prototype
// DOM Registration: HTMLNICartesianGraph
// National Instruments Copyright 2015
//****************************************

(function() {
    'use strict';

    var $ = NationalInstruments.Globals.jQuery;
    var NI_SUPPORT = NationalInstruments.HtmlVI.NISupport;
    var graphDataPipeline = NationalInstruments.HtmlVI.DataPipeline.GraphDataPipeline;

    var plotsUpdatesEnabled = true;
    var graphConfigPending = false;
    var seriesLength = 0;
    const defaultPlotColors = [
        'rgba(123, 22, 26, 1)',
        'rgba(0, 142, 228, 1)',
        'rgba(226, 182, 131, 1)',
        'rgba(183, 172, 31, 1)',
        'rgba(174, 220, 239, 1)',
        'rgba(160, 139, 176, 1)',
        'rgba(127, 127, 127, 1)',
        'rgba(60, 7, 38, 1)'
    ];

    class CartesianGraphBase extends window.JQX.BaseElement {
        getSettings() {
            var settings = {
                lines: {
                    show: true,
                    lineWidth: 1
                },
                series: {
                    shadowSize: 0, // Drawing is faster without shadows
                    points: {
                        lineWidth: 1 // in pixels
                        //,fill: false // it's way faster if we don't fill the shapes. TODO: enable this
                    }
                },
                axisLabels: {
                    show: true
                },
                xaxes: [{}],
                yaxes: [{}],
                zoom: {
                    interactive: true,
                    trigger: null
                },
                pan: {
                    interactive: true,
                    enableTouch: true
                },
                selection: {
                    mode: null
                },
                grid: {},
                colors: defaultPlotColors
            };

            return settings;
        };

        static get defaultPlotColors() {
            return defaultPlotColors;
        };

        configureGraph() {
            var i;
            var cartesianGraphSettings = this.getSettings();
            if (this.plotAreaMargin !== '') {
                cartesianGraphSettings.grid.margin = JSON.parse(this.plotAreaMargin);
            }

            // Apply Settings from host
            cartesianGraphSettings.xaxes = [];
            cartesianGraphSettings.yaxes = [];
            cartesianGraphSettings.cursors = [];

            for (i = 0; i < this.axes.length; i++) {
                var axis = this.axes[i];
                var a = axis.getViewConfig();

                if (a) {
                    if (a.position && ((a.position === 'left') || (a.position === 'right'))) {
                        cartesianGraphSettings.yaxes.push(a);
                    }

                    if (a.position && ((a.position === 'top') || (a.position === 'bottom'))) {
                        cartesianGraphSettings.xaxes.push(a);
                    }
                }
            }

            for (i = 0; i < this.cursors.length; i++) {
                var cursor = this.cursors[i];
                var c = cursor.getViewConfig();
                cartesianGraphSettings.cursors.push(c);
            }

            if (this.graphTools) {
                cartesianGraphSettings.zoom.interactive = this.graphTools.allowsScrollWheelZoom();
                cartesianGraphSettings.pan.interactive = this.graphTools.allowsPan();
                cartesianGraphSettings.selection.mode = this.graphTools.mode === 'zoom' ? 'smart' : null;
            }

            return cartesianGraphSettings;
        };

        updateGraphConfig() {
            var childElement = this.graphdiv;
            if (childElement === undefined) {
                return; // not ready yet
            }

            var cartesianGraphData = this.createData(this.plots, this.convertToFlotFormat(this.parsedValue));
            var cartesianGraphSettings = this.configureGraph();
            this.setHover(cartesianGraphData, cartesianGraphSettings);
            this.setGridStyle(this.gridEl, cartesianGraphSettings);

            try {
                this.graph = $.plot(childElement, cartesianGraphData, cartesianGraphSettings);
                var that = this;
                this.graph.getCursors().forEach(function(cursor, i) {
                    /* place a cookie on the cursor */
                    cursor.cookie = that.cursors[i];
                    that.cursors[i].setCursor(cursor);
                });
            } catch (error) {
                NI_SUPPORT.log(error.message);
            }
        };

        onCursorUpdates(event, cursordata) {
            cursordata.forEach(function(cursor, i) {
                cursor.target.cookie.updateCursorElement(cursordata[i]);
            });
        };

        throttlePlotUpdates(throttle) {
            plotsUpdatesEnabled = !throttle;
            if (plotsUpdatesEnabled && graphConfigPending) {
                this.updateGraphConfig(); // keep the graph in sync
                graphConfigPending = false;
            }
        };

        updateGraphConfigWhenPlotsUpdatesAreEnabled() {
            if (plotsUpdatesEnabled) {
                this.updateGraphConfig();
                graphConfigPending = false;
            } else {
                graphConfigPending = true;
            }
        };

        convertToFlotFormat(value) {
            return graphDataPipeline.prototype.toFlot(this, value, this.valueType);
        };

        updatePlotName(index, name) {
            if (name) {
                if (this.plots[index]) {
                    this.plots[index].label = name;
                }
            }
        };

        createData(plots, dataSeries) {
            var plotConfig = [];

            dataSeries.forEach(function(data, i) {
                var plot = plots[i];
                if (Array.isArray(data)) {
                    data = {
                        data: data
                    };
                }

                if (plot) {
                    plotConfig[i] = plot.getViewConfig();
                    plotConfig[i].data = (plot.show) ? data.data : [];
                    // TODO mix the properties in a better way
                    plotConfig[i].flatdata = data.flatdata;
                    plotConfig[i].start = data.start;
                    plotConfig[i].step = data.step;
                    plotConfig[i].decimate = data.decimate;
                    plotConfig[i].decimatePoints = data.decimatePoints;
                } else {
                    plotConfig[i] = data;
                }
            });

            return plotConfig;
        };

        setData(data) {
            var graph = this.graph;
            var dataSeries = this.convertToFlotFormat(data);
            this.parsedValue = data;
            graph.setData(this.createData(this.plots, dataSeries));
            graph.setupGrid();
            graph.draw();

            if (dataSeries.length !== seriesLength) {
                seriesLength = dataSeries.length;
                this.notifyPlotLegend('ni-plot-count-changed', { element: this, plotCount: dataSeries.length });
            }
        };

        static get properties() {
            const properties = Object.assign({}, super.properties, {
                'value': {
                    value: [],
                    type: 'object'
                },

                /** **graph-ref** - a graph id used to bind legends to the graph*/
                'graphRef': {
                    value: '',
                    type: 'string'
                },

                /** **plot-area-margin** - TODO - document this*/
                'plotAreaMargin': {
                    value: '',
                    type: 'string'
                }
            });

            return properties;
        };

        created() {
            super.created();
            this.plots = [];
            this.axes = [];
            this.cursors = [];
            this.plotLegend = null;
            this.cursorLegend = null;
            this.scaleLegend = null;
            this.graphTools = null;
            this.graphdiv = undefined;
            this.tooltip = null;
            this.tooltipHolder = null;
            this.parsedValue = null;
            this.addGraphItemListeners();
        };

        onResize(size) {
            var plot = this.graph;
            if (plot !== undefined) {
                plot.clearTextCache();
                plot.resize();
                plot.setupGrid();
                plot.draw();
            }
        };

        static get listeners() {
            return {
                'resize': 'onResize',
                'resizeEventHack': 'onResize' // This event is fired by the LV Editor framework on resize
            };
        }

        createGraph() {
            var that = this;
            var childElement = document.createElement('div');
            childElement.style.width = '100%';
            childElement.style.height = '100%';
            childElement.className = 'webchart-drawing-layer';
            this.appendChild(childElement);
            var tooltipHolder = document.createElement('div');
            tooltipHolder.style.width = '0px';
            tooltipHolder.style.height = '0px';
            tooltipHolder.style.position = 'relative';
            tooltipHolder.className = 'ni-graph-tooltip-base';
            var tooltip = document.createElement('div');
            tooltip.id = 'tooltip' + NI_SUPPORT.uniqueId();
            tooltip.className = 'ni-graph-tooltip';
            tooltipHolder.appendChild(tooltip);
            this.appendChild(tooltipHolder);
            this.tooltip = tooltip;
            this.tooltipHolder = tooltipHolder;
            var fakeGrid = document.createElement('ni-grid-div');
            fakeGrid.style.visibility = 'hidden';
            this.appendChild(fakeGrid);
            this.gridEl = fakeGrid;

            this.graphdiv = childElement;

            var cartesianGraphData = this.createData(this.plots, this.convertToFlotFormat(this.parsedValue));
            var cartesianGraphSettings = this.configureGraph();
            this.setHover(cartesianGraphData, cartesianGraphSettings);
            this.setGridStyle(this.gridEl, cartesianGraphSettings);

            try {
                this.graph = $.plot(childElement, cartesianGraphData, cartesianGraphSettings);
            } catch (e) {
                NI_SUPPORT.log('Failed to create a flot chart. Make sure that the placeholder element has a non-zero size !');
            }

            $(childElement).bind('plothover', function(event, pos, item) {
                if (item && that.plots[item.seriesIndex]) {
                    var x = item.datapoint[0],
                        y = item.datapoint[1],
                        hoverFormat = that.plots[item.seriesIndex].hoverFormat || '{0}, {1}',
                        str = that.format(hoverFormat, x, y);

                    $(tooltip).html(str)
                        .css({
                            top: item.pageY - $(tooltipHolder).offset().top + 5,
                            left: item.pageX - $(tooltipHolder).offset().left + 5
                        })
                        .fadeIn(100);
                } else {
                    $(tooltip).hide();
                }
            });

            try {
                this.graph.getPlaceholder().bind('cursorupdates', this.onCursorUpdates);
                this.graph.getCursors().forEach(function(cursor, i) {
                    /* place a cookie on the cursor */
                    cursor.cookie = that.cursors[i];
                    that.cursors[i].setCursor(cursor);
                });
            } catch (e) {}
        };

        attached() {
            super.attached();

            this.parsedValue = this.value;
            this.attachChildren();
            this.createGraph();
            this.attachLegends();
        };

        detached() {
            super.detached();
            this.removeChild(this.graphdiv);
            this.graphdiv = undefined;
            this.removeChild(this.tooltipHolder);
            this.tooltip = undefined;
            this.tooltipHolder = undefined;
            this.removeChild(this.gridEl);
            this.gridEl = undefined;
        }

        addGraphItemListeners(childElements) {
            var that = this,
                pushNewPlot = function(graph, plots, newPlot) {
                    var siblings = graph.getElementsByTagName('ni-cartesian-plot'),
                        newOrderedPlotList = [];
                    for (var i = 0; i < siblings.length; i++) {
                        var sibling = siblings[i],
                            alreadyPushed = plots.find(function(p) {
                                return p === sibling;
                            });
                        if (alreadyPushed || sibling === newPlot) {
                            newOrderedPlotList.push(sibling);
                        }
                    }
                    return newOrderedPlotList;
                };
            that.addEventListener('ni-cartesian-plot-attached', function (evt) {
                if (evt.target === that) {
                    that.plots = pushNewPlot(that, that.plots, evt.detail.element);
                    that.updateGraphConfigWhenPlotsUpdatesAreEnabled();
                    that.notifyPlotLegend('ni-cartesian-plot-attached', evt.detail.element);
                }
            });

            that.addEventListener('ni-cartesian-plot-detached', function(evt) {
                var i;

                that.notifyPlotLegend('ni-cartesian-plot-detached', evt.detail.element);

                if (evt.target === that) {
                    for (i = 0; i < that.plots.length; i++) {
                        if (that.plots[i] === evt.detail.element) {
                            that.plots.splice(i, 1);
                            break;
                        }
                    }

                    that.updateGraphConfigWhenPlotsUpdatesAreEnabled();
                }
            });

            that.addEventListener('ni-cartesian-axis-attached', function (evt) {
                if (evt.target === that) {
                    that.axes.push(evt.detail.element);
                    that.updateGraphConfig();
                    that.notifyScaleLegend('ni-cartesian-axis-attached', evt.detail.element);
                }
            });

            that.addEventListener('ni-cartesian-axis-detached', function (evt) {
                var i;

                if (evt.target === that) {
                    for (i = 0; i < that.axes.length; i++) {
                        if (that.axes[i] === evt.detail.element) {
                            that.axes.splice(i, 1);
                            break;
                        }
                    }

                    that.updateGraphConfig();
                    that.notifyScaleLegend('ni-cartesian-axis-detached', evt.detail.element);
                }
            });

            that.addEventListener('ni-cursor-attached', function(evt) {
                if (evt.target === that) {
                    that.cursors.push(evt.detail.element);
                    that.updateGraphConfig();
                    that.notifyCursorLegend('ni-cursor-attached', evt.detail.element);
                }
            });

            that.addEventListener('ni-cursor-detached', function(evt) {
                var i;

                if (evt.target === that) {
                    for (i = 0; i < that.cursors.length; i++) {
                        if (that.cursors[i] === evt.detail.element) {
                            that.cursors.splice(i, 1);
                            break;
                        }
                    }

                    that.updateGraphConfig();
                    that.notifyCursorLegend('ni-cursor-detached', evt.detail.element);
                }
            });

            that.addEventListener('ni-axis-changed', function(evt) {
                that.updateGraphConfig();
                that.notifyScaleLegend('ni-axis-changed', evt.detail.element);
            });

            that.addEventListener('ni-cursor-changed', function(evt) {
                that.updateGraphConfig();
                that.notifyCursorLegend('ni-cursor-changed', evt.detail.element);
            });

            that.addEventListener('ni-cartesian-plot-changed', function(evt) {
                that.updateGraphConfigWhenPlotsUpdatesAreEnabled();
                that.notifyPlotLegend('ni-cartesian-plot-changed', evt.detail.element);
            });

            that.addEventListener('ni-cartesian-plot-renderer-changed', function(evt) {
                that.updateGraphConfigWhenPlotsUpdatesAreEnabled();
                that.notifyPlotLegend('ni-cartesian-plot-renderer-changed', evt.detail.originalSource);
            });

            that.addEventListener('ni-cartesian-plot-renderer-attached', function(evt) {
                that.updateGraphConfigWhenPlotsUpdatesAreEnabled();
                that.notifyPlotLegend('ni-cartesian-plot-renderer-attached', evt.detail.originalSource);
            });

            that.addEventListener('ni-cartesian-plot-renderer-detached', function(evt) {
                that.updateGraphConfigWhenPlotsUpdatesAreEnabled();
                that.notifyPlotLegend('ni-cartesian-plot-renderer-detached', evt.detail.originalSource);
            });

            that.addEventListener('ni-graph-tools-changed', function() {
                that.updateGraphConfig();
            });
        };

        registerPlotLegend(plotLegend) {
            this.plotLegend = plotLegend;
        };

        notifyPlotLegend(name, originalSource) {
            var eventConfig;

            if (this.plotLegend !== null) {
                eventConfig = {
                    bubbles: false,
                    cancelable: true,
                    detail: {
                        element: this,
                        originalSource: originalSource
                    }
                };

                this.plotLegend.dispatchEvent(new CustomEvent(name, eventConfig));
            }
        };

        registerCursorLegend(cursorLegend) {
            var i;
            this.cursorLegend = cursorLegend;
            if (this.cursors.length > 0) {
                for (i = 0; i < this.cursors.length; i++) {
                    this.notifyCursorLegend('ni-cursor-attached', this.cursors[i]);
                }
            }
        };

        notifyCursorLegend(name, originalSource) {
            var eventConfig;

            if (this.cursorLegend !== null) {
                eventConfig = {
                    bubbles: false,
                    cancelable: true,
                    detail: {
                        element: this,
                        originalSource: originalSource
                    }
                };

                this.cursorLegend.dispatchEvent(new CustomEvent(name, eventConfig));
            }
        };

        registerScaleLegend(scaleLegend) {
            var i;
            this.scaleLegend = scaleLegend;
            if (this.axes.length > 0) {
                for (i = 0; i < this.axes.length; i++) {
                    this.notifyScaleLegend('ni-cartesian-axis-attached', this.axes[i]);
                }
            }
        };

        notifyScaleLegend(name, originalSource) {
            var eventConfig;

            if (this.scaleLegend !== null) {
                eventConfig = {
                    bubbles: false,
                    cancelable: true,
                    detail: {
                        element: this,
                        originalSource: originalSource
                    }
                };

                this.scaleLegend.dispatchEvent(new CustomEvent(name, eventConfig));
            }
        };

        registerGraphTools(graphTools) {
            var that = this;
            this.graphTools = graphTools;

            this.updateGraphConfig();

            $(this.graphdiv).on('plotselected', function(event, ranges) {
                that.notifyGraphTools('ni-cartesian-graph-box-selected', {
                    ranges: ranges
                });
            });

            $(this.graphdiv).on('plotpan', function(event, ranges) {
                that.notifyGraphTools('ni-cartesian-graph-interaction', {
                    ranges: ranges
                });
            });

            $(this.graphdiv).on('plotzoom', function(event, ranges) {
                that.notifyGraphTools('ni-cartesian-graph-interaction', {
                    ranges: ranges
                });
            });

            $(this.graphdiv).on('re-center', function(event) {
                that.notifyGraphTools('ni-cartesian-graph-reCenter', event);
            });
        };

        notifyGraphTools(name, originalSource) {
            var eventConfig;

            if (this.graphTools !== null) {
                eventConfig = {
                    bubbles: false,
                    cancelable: true,
                    detail: {
                        element: this,
                        originalSource: originalSource
                    }
                };

                this.graphTools.dispatchEvent(new CustomEvent(name, eventConfig));
            }
        };

        attachChildren() {
            var graphElement = this,
                children = this.children,
                tagsAndClasses = {
                    'NI-COLOR-SCALE': window.JQX.ColorScale,
                    'NI-CARTESIAN-PLOT': window.JQX.CartesianPlot,
                    'NI-CURSOR': window.JQX.Cursor,
                    'NI-CARTESIAN-AXIS': window.JQX.CartesianAxis
                };

            for (var i = 0; i < children.length; i++) {
                var element = children[i];
                if (tagsAndClasses[element.tagName] && element instanceof tagsAndClasses[element.tagName] && element.isReady) {
                    element.attachToGraph(graphElement);
                }
            }
        };

        // find the graph legend elements that were created before the graph element and attach them
        attachLegends() {
            var graphElement = this,
                legendTags = [
                    'ni-plot-legend'.toUpperCase(),
                    'ni-scale-legend'.toUpperCase(),
                    'ni-cursor-legend'.toUpperCase(),
                    'ni-graph-tools'.toUpperCase()
                ],

                children = this.parentElement.children,
                myLegends = [];

            for (var i = 0; i < children.length; i++) {
                var element = children[i];
                if ((legendTags.indexOf(element.tagName) !== -1) && (element.graphName === this.graphRef || element.graphName === this.niControlId)) {
                    // this is a legend element that references this graph element
                    myLegends.push(element);
                }
            }

            myLegends.forEach(function(legend) {
                if (legend.isReady && legend.attachToGraph) {
                    legend.attachToGraph(graphElement);
                }
            });
        };

        propertyChangedHandler(propertyName, oldValue, newValue) {
            super.propertyChangedHandler(propertyName, oldValue, newValue);
            switch (propertyName) {
                case 'value':
                    this.setData(this.value);
                    break;
                case 'plotAreaMargin':
                    this.updateGraphConfig();
                    break;
            }
        };

        format() {
            var args = arguments;
            var str = args[0];
            return str.replace(/{(\d+)}/g, function(match, number) {
                var index = parseInt(number) + 1;
                var item = args[index];

                if (typeof item !== 'undefined') {
                    var len1 = item.toFixed(2).length;
                    var len2 = item.toExponential(2).length;
                    if (len1 < len2) {
                        return item.toFixed(2);
                    } else {
                        return item.toExponential(2);
                    }
                } else {
                    return match;
                }
            });
        };

        setHover(graphData, graphSettings) {
            var i,
                isHoverable = false;

            for (i = 0; i < graphData.length; i++) {
                if (graphData[i].hoverable) {
                    isHoverable = true;
                    break;
                }
            }

            if (isHoverable === true) {
                if (graphSettings.grid === undefined) {
                    graphSettings.grid = {};
                }

                graphSettings.grid.hoverable = isHoverable;
            } else {
                if (this.tooltip !== null) {
                    $(this.tooltip).hide();
                }
            }
        };

        setGridStyle(gridEl, graphSettings) {
            var grid = graphSettings.grid;
            var style = window.getComputedStyle(gridEl);
            var borderStyle = style.borderStyle;

            var borderBottomWidth = style.borderBottomWidth.replace('px', '');
            var borderTopWidth = style.borderTopWidth.replace('px', '');
            var borderLeftWidth = style.borderLeftWidth.replace('px', '');
            var borderRightWidth = style.borderRightWidth.replace('px', '');
            var borderWidth = {
                top: Number(borderTopWidth),
                bottom: Number(borderBottomWidth),
                left: Number(borderLeftWidth),
                right: Number(borderRightWidth)
            };

            var color = style.borderColor;
            var bgColor = style.backgroundColor;

            grid.borderWidth = (borderStyle === 'solid') ? borderWidth : 0;
            grid.borderColor = color;
            grid.color = style.color;
            grid.backgroundColor = bgColor;
        };

        getYAxisIndexByNIControlID(id) {
            var index = 0;

            for (var i = 0; i < this.axes.length; i++) {
                var axis = this.axes[i];
                if ((axis.axisPosition === 'left') || (axis.axisPosition === 'right')) {
                    index++;
                    if (axis.axisRef === id || axis.niControlId === id) {
                        return index;
                    }
                }
            }

            return 1;
        }

        getXAxisIndexByNIControlID(id) {
            var index = 0;

            for (var i = 0; i < this.axes.length; i++) {
                var axis = this.axes[i];
                if ((axis.axisPosition === 'top') || (axis.axisPosition === 'bottom')) {
                    index++;
                    if (axis.axisRef === id || axis.niControlId === id) {
                        return index;
                    }
                }
            }

            return 1;
        }

        template () {
            return '';
        }
    };

    Object.assign(window.NationalInstruments.HtmlVI.Elements, {
        'CartesianGraphBase': CartesianGraphBase
    });
}());
