/*jslint nomen: true, devel:true*/
//****************************************
// Color scale Custom Element
// DOM Registration: HTMLNIColorScale
// National Instruments Copyright 2017
//****************************************

/**
## ni-color-scale

> A color scale added to an intensity graph is used to configure the color
gradients.

```html
<ni-intensity-graph>
  <ni-cartesian-axis ...></ni-cartesian-axis>
  <ni-cartesian-axis ...></ni-cartesian-axis>
  <ni-color-scale show show-label label="Color Scale" low-color="blue" high-color="red">
  </ni-color-scale>
</ni-intensity-graph>
```

A single color scale can be added to an intensity graph to visualize and control
the maping from values to the color gradient displayed.

Color scales are only supported by intensity graphs, when added to other types of
webcharts they do nothing.

The color gradients are described by an array of stops, for example this:

```js
[
{"value":0,"color":"rgba(0,0,0,1)"},
{"value":0.5,"color":"rgba(0,0,255,1)"},
{"value":1,"color":"rgba(255,255,255,1)"}
]
```

describes a color gradient going from black at the stop with the value 0, to
blue at the stop with the value 0.5 and then to white at the stop with the value
1.
*/

(function() {
    'use strict';

    var COLORSCALE_TYPE = JQX.IntensityGraph.ColorscaleType;

    class ColorScale extends window.JQX.BaseElement {
        /** ### ni-color-scale properties*/
        static get properties() {
            const props = Object.assign({}, super.properties, {

                /** **label** (string) - the name of the color scale. Default: '' */
                'label': {
                    value: '',
                    type: 'string'
                },

                /** **show** (boolean) - if true the color scale will be visible. Default: false */
                'show': {
                    value: false,
                    type: 'boolean'
                },

                /** **show-label** (boolean) - if true the color scale label will be visible. Default: false */
                'showLabel': {
                    value: false,
                    type: 'boolean'
                },

                /** **axis-position** (string) - the position of the color scale. Default: 'right'.
                Accepted values: *'left'*, *'right'* */
                'axisPosition': {
                    value: 'right',
                    type: 'string'
                },

                /** **show-tick-labels** (string) - which of the tick labels are visible.
                Possible values: 'none', 'endpoints', 'major' and 'all'. Default: 'all'.
                For 'endpoints' only the minimum and the maximum values will be visible
                at the edges of the axis. To make some intermediary values visible use
                'major', and 'all' to show all of them.*/
                'showTickLabels': {
                    value: 'all',
                    type: 'string'
                },

                /** **high-color** (string) - the color assigned to the values bigger than
                the color scale maximum. Default: '#ffffff'.
                Accepted values: a CSS color string*/
                'highColor': {
                    value: '#ffffff',
                    type: 'string'
                },

                /** **low-color** (string) - the color assigned to the values smaller than
                the color scale minimum. Default: '#000000'.
                Accepted values: a CSS color string*/
                'lowColor': {
                    value: '#000000',
                    type: 'string'
                },

                /** **markers** (string) - the color gradient used to map values to colorScaleAxis.

                Default: ''.
                TODO: document the gradient format */
                'markers': {
                    // http://colorbrewer2.org/#type=sequential&scheme=Blues&n=3
                    value: `[{
                        "value": 0,
                        "color": "#3182bd"
                    }, {
                        "value": 50,
                        "color": "#9ecae1"
                    }, {
                        "value": 100,
                        "color": "#deebf7"
                    }]`,
                    type: 'string'
                },

                /** **auto-scale** (string) - if 'exact' the color gradient will strech to adapt to the
                range of the data set provided. Default: 'auto'*/
                'autoScale': {
                    value: 'auto',
                    type: 'string'
                },

                /** **minimum** (number) - the minimum axis value. Default: 0*/
                'minimum': {
                    value: 0,
                    type: 'number'
                },

                /** **maximum** (number) - the maximum axis value. Default: 1*/
                'maximum': {
                    value: 1,
                    type: 'number'
                }
            });

            return props;
        };

        created() {
            super.created();

            this._parentGraph = undefined;
        };

        sendEventToParentGraph(name) {
            var eventConfig;

            if (this._parentGraph !== undefined) {
                eventConfig = {
                    bubbles: true,
                    cancelable: true,
                    detail: {
                        element: this
                    }
                };

                this._parentGraph.dispatchEvent(new CustomEvent(name, eventConfig));
            }
        };

        attached() {
            super.attached();

            if (this.parentElement instanceof JQX.IntensityGraph && this.parentElement.isReady) {
                this.attachToGraph(this.parentElement);
            } else {
                this._parentGraph = undefined;
            }
        };

        attachToGraph(graph) {
            if (!this._parentGraph) {
                this._parentGraph = this.parentElement;
                this.sendEventToParentGraph('ni-cartesian-axis-attached');
            }
        };

        propertyChangedHandler(propertyName, oldValue, newValue) {
            super.propertyChangedHandler(propertyName, oldValue, newValue);

            this.sendEventToParentGraph('ni-axis-changed');
        };

        detached() {
            super.detached();

            this.sendEventToParentGraph('ni-cartesian-axis-detached');
            this._parentGraph = undefined;
        };

        setFont(fontSize, fontFamily, fontWeight, fontStyle, textDecoration) {
            this.style.fontSize = fontSize;
            this.style.fontFamily = fontFamily;
            this.style.fontWeight = fontWeight;
            this.style.fontStyle = fontStyle;
            this.style.textDecoration = textDecoration;

            this.sendEventToParentGraph('ni-axis-changed');
        };

        // getViewConfig transforms the configuration from the one used by the custom element to the one understood by Flot */
        getViewConfig() {
            var currStyle = window.getComputedStyle(this),
                grow = (/grow/).test(this.autoScale),
                autoscale = grow ? this.autoScale.replace(/grow/, '') : this.autoScale;

            var flotConfig = {
                type: COLORSCALE_TYPE,
                axisLabel: this.showLabel ? this.label : undefined,
                show: this.show,
                autoscale: ((autoscale === "auto") ? "none" : autoscale),
                growOnly: grow,
                min: this.minimum,
                max: this.maximum,
                position: this.axisPosition,
                lowColor: this.lowColor,
                highColor: this.highColor,
                markers: this.markers,
                showTickLabels: this.showTickLabels,
                font: {
                    size: parseFloat(currStyle.fontSize),
                    style: currStyle.fontStyle,
                    weight: currStyle.fontWeight,
                    family: currStyle.fontFamily,
                    color: 'inherit'
                },
                axisZoom: false,
                plotZoom: false,
                axisPan: false,
                plotPan: false,
                gridLines: false
            };

            return flotConfig;
        };

        template() {
            return '';
        }

        // get the flot axis coresponding to this element
        getFlotAxis() {
            if (this._parentGraph) {
                var colorScaleAxis = this._parentGraph.graph
                    .getYAxes()
                    .filter(function(axis) {
                        return axis.options.type === COLORSCALE_TYPE;
                    })[0];
                return colorScaleAxis;
            }
        };
    };

    JQX('ni-color-scale', ColorScale);
}());
