/**
## ni-cursor

> Add cursors to a graph.

Sometimes you want to take a measurement off a graph you have made. Cursors allow
you to do that.

```html
<ni-cartesian-graph>
  <ni-cartesian-axis show grid-lines show-label label="Axis 1" axis-position='bottom'>
  </ni-cartesian-axis>
  <ni-cartesian-axis show grid-lines show-label label="Axis 2" axis-position='top'>
  </ni-cartesian-axis>
  <ni-cursor label="Cursor 1" show show-label snap-to-plot="0" cursor-color="#40d387"></ni-cursor>
</ni-cartesian-graph>
```
Multiple cursors can be added to a graph and configured.
*/

(function() {
    'use strict';

    var $ = NationalInstruments.Globals.jQuery;

    class Cursor extends window.JQX.BaseElement {
        /** ### ni-cursor properties*/
        static get properties() {
            const props = Object.assign({}, super.properties, {

                /** **show** (boolean) - if true the cursor will be visible. Default: false*/
                'show': {
                    value: false,
                    type: 'boolean'
                },

                /** **label** (string) - the label of the cursor. Default: ''*/
                'label': {
                    value: '',
                    type: 'string'
                },

                /** **show-label** (boolean) - if true the cursor label will be visible.
                Default: false*/
                'showLabel': {
                    value: false,
                    type: 'boolean'
                },

                /**
                **target-shape** (string) - the shape of cursor. Default: 'ellipse'.
                Accepted values: 'ellipse', 'square', 'diamond', 'triangle', 'cross'*/
                'targetShape': {
                    value: 'ellipse',
                    type: 'string'
                },

                /**
                **color** (string) - the color of the cursor. Default: 'black'.
                Accepted values: a CSS color string*/
                'cursorColor': {
                    value: '#2B3033',
                    type: 'string'
                },

                /**
                **crosshair-style** (string) - the shape of crosshair. Default: 'both'.
                Accepted values: 'both', 'vertical', 'horizontal', 'none'*/
                'crosshairStyle': {
                    value: 'both',
                    type: 'string'
                },

                /** **show-value** (boolean) - if true the coordinates of the cursor will
                be visible. Default: false*/
                'showValue': {
                    value: false,
                    type: 'boolean'
                },

                /** **snap-to-plot** (number) - the number of the plot or -1 to snap to any plot.
                Default: undefined (no snapping)*/
                'snapToPlot': {
                    value: undefined,
                    type: 'number?'
                },

                /** **interpolate** (boolean) - if true the cursor will do a linear interpolation
                between points when modiffying position. Default: false
                */
                'interpolate': {
                    value: false,
                    type: 'boolean'
                },

                /** **xaxis** (string) - the X axis that this cursor will use when snap-to-plot is undefined.
                Default: ''. Accepted values: an axis-ref. In case an xaxis with the specified axis-ref
                is not found the first xaxis is used*/
                'xaxis': {
                    value: '',
                    type: 'string'
                },

                /** **yaxis** (string) - the Y axis that this cursor will use when snap-to-plot is undefined.
                Default: ''. Accepted values: an axis-ref. In case an yaxis with the specified axis-ref
                is not found the first yaxis is used*/
                'yaxis': {
                    value: '',
                    type: 'string'
                },

                /** **x** (number) - the x target coordinate of the cursor. This coordinate
                is relative to the visible area of the graph, in the interval 0 to 1.
                0 is on the left edge and 1 on the one to the right.
                Default: 0.5*/
                'x': {
                    value: 0.5,
                    type: 'number'
                },

                /** **y** (number) - the y target coordinate of the cursor. This coordinate
                is relative to the visible area of the graph, in the interval 0 to 1.
                0 is on the bottom edge and 1 on the top one.
                Default: 0.5*/
                'y': {
                    value: 0.5,
                    type: 'number'
                },

                /** **font-family** (string) - the font used for cursor label and values.
                Default: 'sans-serif'*/
                'fontFamily': {
                    value: 'sans-serif',
                    type: 'string'
                },

                /** **font-size** (string) - the font size used for cursor label and values.
                Default: '10px'*/
                'fontSize': {
                    value: '10px',
                    type: 'string'
                },

                /** **font-style** (string) - the font style used for cursor label and values.
                Default: ''*/
                'fontStyle': {
                    value: '',
                    type: 'string'
                },

                /** **font-size** (string) - the font weight used for cursor label and values.
                Default: '10px'*/
                'fontWeight': {
                    value: '',
                    type: 'string'
                }
            });

            return props;
        };

        created() {
            super.created();
            this.cursor = null;
            this._parentGraph = undefined;
        };

        sendEventToParentGraph(name) {
            var eventConfig;

            if (this._parentGraph !== undefined) {
                eventConfig = {
                    bubbles: true,
                    cancelable: true,
                    detail: {
                        element: this
                    }
                };

                this._parentGraph.dispatchEvent(new CustomEvent(name, eventConfig));
            }
        };

        attached() {
            super.attached();

            if ((this.parentElement instanceof JQX.CartesianGraph || this.parentElement instanceof JQX.Chart) &&
                this.parentElement.isReady) {
                this.attachToGraph(this.parentElement);
            } else {
                this._parentGraph = undefined;
            }
        };

        attachToGraph(graph) {
            if (!this._parentGraph) {
                this._parentGraph = graph;
                this.sendEventToParentGraph('ni-cursor-attached');
            }
        };

        /** TODO: clean up and document the events generated by cursors*/
        propertyChangedHandler(propertyName, oldValue, newValue) {
            super.propertyChangedHandler(propertyName, oldValue, newValue);

            this.sendEventToParentGraph('ni-cursor-changed');
        };

        detached() {
            super.detached();

            this.sendEventToParentGraph('ni-cursor-detached');
            this._parentGraph = undefined;
        };

        setCursor(cursor) {
            this.cursor = cursor;
        };

        updateCursorElement(data) {
            this.x = this.cursor.position.relativeX;
            this.y = this.cursor.position.relativeY;
            if (this._parentGraph) {
                $(this).trigger('cursorUpdated', data);
            }
        };

        getViewConfig () {
            var style = window.getComputedStyle(this);
            var crosshairStyle = this.crosshairStyle;
            var mode;

            if (crosshairStyle === 'both') {
                mode = 'xy';
            } else if (crosshairStyle === 'horizontal') {
                mode = 'y';
            } else if (crosshairStyle === 'vertical') {
                mode = 'x';
            } else if (crosshairStyle === 'none') {
                mode = '';
            } else {
                mode = null;
            }

            return {
                show: this.show,
                showLabel: this.showLabel,
                showValues: this.showValue,
                name: this.label,
                color: this.cursorColor,
                snapToPlot: this.snapToPlot,
                defaultxaxis: this._parentGraph.getXAxisIndexByNIControlID(this.xaxis),
                defaultyaxis: this._parentGraph.getYAxisIndexByNIControlID(this.yaxis),
                mode: mode,
                symbol: this.targetShape,
                fontSize: style.fontSize,
                fontFamily: style.fontFamily,
                fontStyle: style.fontStyle,
                fontWeight: style.fontWeight,
                position: {
                    relativeX: this.x,
                    relativeY: this.y
                }
            };
        };

        setFont(fontSize, fontFamily, fontWeight, fontStyle, textDecoration) {
            this.style.fontSize = fontSize;
            this.style.fontFamily = fontFamily;
            this.style.fontWeight = fontWeight;
            this.style.textDecoration = textDecoration;

            this.sendEventToParentGraph('ni-cursor-changed');
        };

        template() {
            return '';
        }
    };

    JQX('ni-cursor', Cursor);
}());
