/**
# TypedWebCharts Plugin

> Allows setting data types to WebCharts.

The information of the data type will be used to optimize setting
or pushing new data to the WebCharts.

Example:
```html
<ni-cartesian-graph value="[[1, 2, 3, 4, 5]]" ni-type="{&quot;name&quot;:&quot;Array&quot;,&quot;rank&quot;:1,&quot;subtype&quot;:{&quot;name&quot;:&quot;Double&quot;}}">
</ni-cartesian-graph>
```

*/

(function() {
    'use strict';

    var DataPipeline = NationalInstruments.HtmlVI.DataPipeline,
        TypedHistoryBuffer = NationalInstruments.HtmlVI.DataPipeline.TypedHistoryBuffer;

    /** ## ni-cartesian-graph module */
    class NITypedCartesianGraphModule {
        static get properties() {
            const properties = {
                /** **ni-type** - the data type of the values being written to the
                value property or setData() method */
                'niType': {
                    value: undefined,
                    type: 'string?',
                    observer: 'setNIType'
                }
            };
            return properties;
        }

        static get moduleName() {
            return 'NITypedCartesianGraphModule';
        }

        setNIType(oldValue, newValue) {
            this.ownerElement.valueTypeNeedsUpdate = true;
        }

        created() {
            this.ownerElement.valueType = DataPipeline.DataTypesEnum.unknown;
            this.ownerElement.valueTypeNeedsUpdate = false;
        }

        attached() {
            this.ownerElement.valueType = DataPipeline.inferDataTypeFromNIType(createNIType(this.ownerElement.niType));
            this.ownerElement.valueTypeNeedsUpdate = false;
        }

        setData(data) {
            if (this.ownerElement.valueTypeNeedsUpdate) {
                this.ownerElement.valueType = DataPipeline.inferDataTypeFromNIType(createNIType(this.ownerElement.niType));
                this.ownerElement.valueTypeNeedsUpdate = false;
            }
            window.NationalInstruments.HtmlVI.Elements.CartesianGraphBase.prototype.setData.call(this.ownerElement, data)
        }
    }

    /** ## ni-cartesian-graph module */
    class NITypedChartModule {
        static get properties() {
            const properties = {
                /** **ni-type** - the data type of the values being pushed to the history buffer. */
                'niType': {
                    value: undefined,
                    type: 'string?',
                    observer: 'setNIType'
                }
            };
            return properties;
        }

        static get moduleName() {
            return 'NITypedChartModule';
        }

        setNIType(oldValue, newValue) {
            setNITypeToHistoryBuffer(this.ownerElement.historyBuffer, this.ownerElement.niType);
        }

        ready() {
            this.ownerElement.historyBuffer = new TypedHistoryBuffer(this.ownerElement.bufferSize, 1);
            setNITypeToHistoryBuffer(this.ownerElement.historyBuffer, this.ownerElement.niType);
        }

        setHistoryBuffer(historyBuffer) {
            this.ownerElement.historyBuffer = historyBuffer;
            setNITypeToHistoryBuffer(this.ownerElement.historyBuffer, this.ownerElement.niType);
        }
    }

    /** ## ni-intensity-graph module */
    class NITypedIntensityGraphModule {
        static get properties() {
            const properties = {
                /** **ni-type** - for now this is not used */
                'niType': {
                    value: NITypes.DOUBLE.makeArray(2).toJSON(),
                    type: 'string?'
                }
            };
            return properties;
        }

        static get moduleName() {
            return 'NITypedIntensityGraphModule';
        }
    }

    function createNIType(descriptor) {
        var niType = null;
        try {
            if (descriptor) {
                niType = new NIType(descriptor);
            }
        } catch (err) {}
        return niType;
    }

    function setNITypeToHistoryBuffer(historyBuffer, niTypeStr) {
        if (typeof historyBuffer.setNIType === 'function') {
            historyBuffer.setNIType(createNIType(niTypeStr));
        }
    };

    JQX.Elements.whenRegistered('ni-cartesian-graph', function(proto) {
        proto.addModule(NITypedCartesianGraphModule);
    });

    JQX.Elements.whenRegistered('ni-chart', function(proto) {
        proto.addModule(NITypedChartModule);
    });

    JQX.Elements.whenRegistered('ni-intensity-graph', function(proto) {
        proto.addModule(NITypedIntensityGraphModule);
    });
}());
