/*jslint nomen: true, devel:true*/
//****************************************
// Cartesian Plot Renderer
// DOM Registration: HTMLNICartesianPlotRenderer
// National Instruments Copyright 2017
//****************************************

/**
## ni-cartesian-plot-renderer

> Describes the way a plot is rendered

The four ways to render a plot are the line renderer, the area fill renderer,
the points renderer and the bar renderer. You can combine these in any way you
like for a single plot and different plots can have different renderers.

The fastest one is the line renderer and this is the recomended one for large
data sets and if you care about performance.

The render can be further customized by using the renderer properties.
*/

(function() {
    'use strict';

    var toFillTowards = function(baseLine) {
        switch (baseLine) {
            case 'zero':
                return 0;
            case 'infinity':
                return Infinity;
            case 'positiveinfinity':
                return Infinity;
            case '-infinity':
                return -Infinity;
            case 'negativeinfinity':
                return -Infinity;
            default:
                return undefined;
        }
    };

    var areaFillOpacity = 0.6;

    class CartesianPlotRenderer extends window.JQX.BaseElement {
        /** ### ni-cartesian-plot-renderer properties */
        static get properties() {
            const props = Object.assign({}, super.properties, {

                /** **line-stroke** (string) - the color of the line drawn. Default: 'auto'.
                Accepted values: a CSS color string, an empty string or 'auto'. If an empty string
                is passed the line rendering for the plot is disabled. If 'auto' is passed a color
                will be automatically assigned to the plot.
                */
                'lineStroke': {
                    value: 'auto',
                    type: 'string'
                },

                /** **line-width** (number) - the width of the line. Use 1 for maximum
                performance as thicker lines can be slower, depending on the browser and
                the graphic card used. Default: 1*/
                'lineWidth': {
                    value: 1,
                    type: 'number'
                },

                /** **line-style** (string) - the style of the line drawn. Default: 'solid'.
                Accepted values: *'solid'*, *'dot'*, *'mediumdash'*, *'dashdot'* and *'largedash'*
                */
                'lineStyle': {
                    value: 'solid',
                    type: 'string'
                },

                /** **area-fill** (string) - the color of the area fill drawn under/over
                the line. Default: ''.
                Accepted values: a CSS color string, an empty string or auto. If an empty string
                is passed the area fill for the plot is disabled. If 'auto' is passed a color
                will be automatically assigned to the fill.
                */
                'areaFill': {
                    value: '',
                    type: 'string'
                },

                /**
                **area-base-line** (string) - determines the direction the area fill.
                Default: 'zero'. Accepted values:

                * 'zero': fill the line towards 0
                * '-infinity': fill the line towards -Infinity.
                * 'infinity': fill the line towards Infinity.
                */
                'areaBaseLine': {
                    value: 'zero',
                    type: 'string'
                },

                /** **bar-fill** (string) - the color of the bars drawn. Default: ''.
                Accepted values: a CSS color string, an empty string or auto. If an empty
                string is passed the bars rendering for the plot is disabled. If 'auto'
                is passed a color will be automatically assigned to the bars.
                */
                'barFill': {
                    value: '',
                    type: 'string'
                },

                /**
                **bar-base-line** (string) - determines the direction the bar fill.
                Default: 'zero'. Accepted values:

                * 'zero': fill the line towards 0
                * '-infinity': fill the line towards -Infinity.
                * 'infinity': fill the line towards Infinity.
                */
                'barBaseLine': {
                    value: 'zero',
                    type: 'string'
                },

                /** **point-color** (string) - the color of the points drawn. Default: ''.
                Accepted values: a CSS color string, an empty string or auto. If an empty
                string is passed the points rendering for the plot is disabled. If 'auto'
                is passed a color will be automatically assigned to the points.
                */
                'pointColor': {
                    value: '',
                    type: 'string'
                },

                /** **point-size** (number) - the size of the points drawn. Default: 5.*/
                'pointSize': {
                    value: 5,
                    type: 'number'
                },

                /** **point-shape** (string) - determines the shape of the points.
                Default: 'ellipse'. Accepted values: 'ellipse', 'square', 'diamond',
                'triangle', 'cross'
                */
                'pointShape': {
                    value: 'ellipse',
                    type: 'string'
                }
            });

            return props;
        };

        created() {
            super.created();

            // Public Instance Properties
            // None

            // Private Instance Properties
            this._parentPlot = undefined;
        };

        sendEventToParentPlot(name) {
            var eventConfig;

            if (this._parentPlot !== undefined) {
                eventConfig = {
                    bubbles: false,
                    cancelable: true,
                    detail: {
                        element: this
                    }
                };

                this._parentPlot.dispatchEvent(new CustomEvent(name, eventConfig));
            }
        };

        attached() {
            super.attached();

            if (this.parentElement instanceof JQX.CartesianPlot && this.parentElement.isReady) {
                this.attachToPlot(this.parentElement);
            } else {
                this._parentPlot = undefined;
            }
        };

        attachToPlot(plot) {
            this._parentPlot = plot;
            this.sendEventToParentPlot('ni-cartesian-plot-renderer-attached');
        };

        propertyChangedHandler(propertyName, oldValue, newValue) {
            super.propertyChangedHandler(propertyName, oldValue, newValue);

            this.sendEventToParentPlot('ni-cartesian-plot-renderer-changed');
        };

        detached() {
            super.detached();

            this.sendEventToParentPlot('ni-cartesian-plot-renderer-detached');
        };

        toFlotLineStyle(style) {
            switch (style) {
                case 'solid':
                    return undefined;
                case 'dot':
                    return [this.lineWidth, this.lineWidth * 2];
                case 'mediumdash':
                    return [this.lineWidth * 3, this.lineWidth * 2];
                case 'dashdot':
                    return [this.lineWidth * 3, this.lineWidth * 2, this.lineWidth, this.lineWidth * 2];
                case 'largedash':
                    return [this.lineWidth * 5, this.lineWidth * 3];
                default:
                    return undefined;
            }
        };

        getColor() {
            var color = this.lineStroke || this.pointColor || this.areaFill || this.barFill;
            if (color === 'auto') {
                return null;
            }

            return color;
        };

        getViewConfig() {
            return {
                color: this.getColor(),
                lines: {
                    show: this.lineStroke !== '' || this.areaFill !== '',
                    lineWidth: this.lineWidth,
                    fill: this.areaFill !== '' ? areaFillOpacity : false,
                    zero: false,
                    fillTowards: toFillTowards(this.areaBaseLine),
                    dashes: this.toFlotLineStyle(this.lineStyle)
                },
                points: {
                    show: this.pointColor !== '',
                    radius: this.pointSize ? this.pointSize / 2 : 2,
                    symbol: this.pointShape
                },
                bars: {
                    show: this.barFill !== '',
                    barWidth: 0.75, // TODO this is only valid if the distance between points is 1
                    align: 'center',
                    zero: false,
                    fillTowards: toFillTowards(this.barBaseLine)
                }
            };
        };

        template () {
            return '';
        }
    };

    JQX('ni-cartesian-plot-renderer', CartesianPlotRenderer);
}());
