/*jslint nomen: true, devel:true*/
//****************************************
// Cartesian Plot
// DOM Registration: HTMLNICartesianPlot
// National Instruments Copyright 2017
//****************************************

/**
## ni-cartesian-plot

> A custom element that, along with a nested ni-cartesian-plot-renderer describes
the way a data plot is rendered.

A plot is the graphic representation of a data set. A graph or chart can show
simultaneously multiple data sets (plots). To customize how a data set, or plot
is rendered use ni-cartesian-plot elements nested inside a graph or chart
element.

```html
<ni-cartesian-graph>
  <ni-cartesian-axis axis-ref="axis1ref" show axis-position="bottom"></ni-cartesian-axis>
  <ni-cartesian-axis axis-ref="axis2ref" show axis-position="left"></ni-cartesian-axis>
  <ni-cartesian-plot show label="plot 1" xaxis="axis1ref" yaxis="axis2ref">
    add an ni-cartesian-plot-renderer here
  </ni-cartesian-plot>
</ni-cartesian-graph>
```
*/

(function() {
    'use strict';

    // Static Private Variables
    var defaultPlotConfig = {
        lines: {
            show: false
        }
    };

    class CartesianPlot extends window.JQX.BaseElement {
        /** ### ni-cartesian-plot properties */
        static get properties() {
            const props = Object.assign({}, super.properties, {

                /** **show** (boolean) - if true the plot will be shown. Default: false */
                'show': {
                    value: false,
                    type: 'boolean'
                },

                /** **label** (string) - the name of the plot. Default: '' */
                'label': {
                    value: '',
                    type: 'string'
                },

                /** **xaxis** (string) - the X axis that this plot will use. Default: ''.
                Accepted values: an axis-ref. In case an xaxis with the specified axis-ref
                is not found the first xaxis is used*/
                'xaxis': {
                    value: '',
                    type: 'string'
                },

                /** **yaxis** (string) - the Y axis that this plot will use. Default: ''.
                Accepted values: an axis-ref. In case an yaxis with the specified axis-ref
                is not found the first yaxis is used*/
                'yaxis': {
                    value: '',
                    type: 'string'
                },

                /** **enable-hover** (boolean) - if true, when the mouse hovers close to a point on the plot
                a tooltip with the point value will be shown */
                'enableHover': {
                    value: false,
                    type: 'boolean'
                },

                /** **hover-format** (string) - specifies the format of the text shown in the tooltip. TODO: document the format*/
                'hoverFormat': {
                    value: '',
                    type: 'string'
                }
            });

            return props;
        };

        created() {
            super.created();
            this._parentGraph = undefined;
        };

        sendEventToParentGraph (name, originalSource) {
            var eventConfig;

            if (this._parentGraph !== undefined) {
                eventConfig = {
                    bubbles: false,
                    cancelable: true,
                    detail: {
                        element: this,
                        originalSource: originalSource
                    }
                };

                this._parentGraph.dispatchEvent(new CustomEvent(name, eventConfig));
            }
        };

        attached () {
            super.attached();

            if ((this.parentElement instanceof JQX.CartesianGraph || this.parentElement instanceof JQX.Chart) &&
                this.parentElement.isReady) {
                this.attachToGraph(this.parentElement);
            } else {
                this._parentGraph = undefined;
            }
        };

        attachToGraph(graph) {
            if (!this._parentGraph) {
                this._parentGraph = graph;
                this.addPlotItemListeners();
                this.attachChildren();
                this.sendEventToParentGraph('ni-cartesian-plot-attached');
            }
        };

        attachChildren() {
            var graphElement = this,
                children = this.children;

            for (var i = 0; i < children.length; i++) {
                var element = children[i];
                if (element.tagName === 'NI-CARTESIAN-PLOT-RENDERER' && element instanceof window.JQX.CartesianPlotRenderer && element.isReady) {
                    element.attachToPlot(graphElement);
                }
            }
        };

        addPlotItemListeners () {
            var that = this;
            that.addEventListener('ni-cartesian-plot-renderer-attached', function(evt) {
                if (evt.target === that) {
                    this.plotRenderer = evt.detail.element;
                    this.sendEventToParentGraph('ni-cartesian-plot-renderer-attached', evt.detail.element);
                }
            });

            that.addEventListener('ni-cartesian-plot-renderer-detached', function(evt) {
                if (evt.target === that) {
                    this.plotRenderer = undefined;
                    this.sendEventToParentGraph('ni-cartesian-plot-renderer-detached', evt.detail.element);
                }
            });

            that.addEventListener('ni-cartesian-plot-renderer-changed', function(evt) {
                if (evt.target === that) {
                    this.sendEventToParentGraph('ni-cartesian-plot-renderer-changed', evt.detail.element);
                }
            });
        };

        detached () {
            super.detached();

            this.sendEventToParentGraph('ni-cartesian-plot-detached');
            this._parentGraph = undefined;
        };

        propertyChangedHandler(propertyName, oldValue, newValue) {
            super.propertyChangedHandler(propertyName, oldValue, newValue);

            this.sendEventToParentGraph('ni-cartesian-plot-changed');
        };

        getViewConfig () {
            var plotConfig = defaultPlotConfig;
            if (this.plotRenderer !== undefined && this.show === true) {
                plotConfig = this.plotRenderer.getViewConfig();
            }

            plotConfig.hoverable = this.enableHover;
            plotConfig.xaxis = this._parentGraph.getXAxisIndexByNIControlID(this.xaxis);
            plotConfig.yaxis = this._parentGraph.getYAxisIndexByNIControlID(this.yaxis);

            return plotConfig;
        };

        template () {
            return '';
        }
    };

    JQX('ni-cartesian-plot', CartesianPlot);
}());
