(function () {
    'use strict';

    NationalInstruments.HtmlVI.DataPipeline = {};

    NationalInstruments.HtmlVI.DataPipeline.decimateAW = function (series, start, end, buckets) {
        var startIndex = (start - series.start) / series.step,
            endIndex = (end - series.start) / series.step;

        return NationalInstruments.HtmlVI.DataPipeline.decimate1D(series, startIndex, endIndex, buckets);
    }

    // computes min and max for a data points array with the specified range (from start, inclusive to the end, exclusive)
    // and add them to the result. data is an array containing x coordinates alternating with y coordinates
    var minMaxForRange = function (data, s, e, result) {
        var max = -Infinity,
            maxIndex = -1,
            min = Infinity,
            minIndex = -1;

        for (var i = s * 2; i < e * 2; i += 2) {
            if (data[i + 1] < min) {
                minIndex = data[i];
                min = data[i + 1];
            }
            if (data[i + 1] > max) {
                maxIndex = data[i];
                max = data[i + 1];
            }
        }

        if (maxIndex === -1) {
            return;
        }

        if (maxIndex === minIndex) {
            result.push(minIndex);
            result.push(min);
        } else {
            if (maxIndex < minIndex) {
                result.push(maxIndex);
                result.push(max);
                result.push(minIndex);
                result.push(min);
            } else {
                result.push(minIndex);
                result.push(min);
                result.push(maxIndex);
                result.push(max);
            }
        }
    }

    /* given a data series with a nonlinear, monothonic transformation function on the x axis,
    it splits the interval between s and e in buckets of equal "pixel width" */
    var generateBucketIndexes = function(series, s, e, width, buckets) {
        var len = series.datapoints.points.length / 2,
            step,
            transformedStart, transformedEnd,
            firstSample, lastSample,
            transform = series.xaxis.options.transform,
            inverseTransform = series.xaxis.options.inverseTransform;

        transformedStart = transform(s);
        transformedEnd = transform(e);
        step = (transformedEnd - transformedStart) / width;

        if (transformedEnd <= transformedStart || step <= 0 || len === 0) {
            return;
        }

        for (var i = transformedStart; i < transformedEnd + step; i += step) {
            firstSample = inverseTransform(i);
            lastSample = inverseTransform(i + step);

            if (lastSample <= 0 || firstSample >= len) {
                continue;
            }

            if (firstSample < 0) {
                firstSample = 0;
            }

            if (lastSample > len) {
                lastSample = len;
            }

            firstSample = Math.ceil(firstSample);
            lastSample = Math.floor(lastSample);

            if (firstSample > lastSample) {
                continue;
            }

            buckets.push(firstSample);
            buckets.push(lastSample);
        }

        extendBuckets(s, e, len, buckets);
    }

    /* extends the buckets so that they contain also the points imediately before and after the interval
    so in the case of line graphs the lines entering into the first visible point and the line leaving the
    last visisble point are also drawn.*/
    var extendBuckets = function (start, end, len, buckets) {
        var bucketsLength = buckets.length;
        var firstValue, lastValue;

        if (bucketsLength === 0) {
            buckets.push(Math.floor(start));
            buckets.push(Math.floor(start) + 1);
        } else if (bucketsLength === 2) {
            if (buckets[0] > 0) {
                buckets[0]--;
            }

            if (buckets[1] < len) {
                buckets[1]++;
            }
        } else {
            firstValue = buckets[0];
            lastValue = buckets[bucketsLength - 1];

            if (firstValue > 0) {
                buckets.unshift(firstValue - 1);
                buckets.unshift(firstValue - 1);
            }

            if (lastValue < len) {
                buckets.push(lastValue + 1);
                buckets.push(lastValue + 1);
            }
        }
    }

    /* decimates a data series with a log x axis */
    NationalInstruments.HtmlVI.DataPipeline.decimate1DLog = function (series, start, end, width) {
        var buckets = [],
            result = [],
            data = series.datapoints.points;

        generateBucketIndexes(series, start, end, width, buckets);

        for (var i = 0; i < buckets.length; i += 2) {
            minMaxForRange(data, buckets[i], buckets[i + 1] + 1, result);
        }

        return result;
    }

    /* decimates a data series. If the x axis of the series is logarithmic this function calls decimate1DLog*/
    NationalInstruments.HtmlVI.DataPipeline.decimate1D = function (series, start, end, buckets) {
        var i,
            index = 0,
            iindex = 0,
            length = 0,
            step = buckets / (end - start),
            data = series.datapoints.points,
            res = [],
            newBucket = true,
            max = -Infinity, maxIndex = -1,
            min = Infinity, minIndex = -1;

        if (series.xaxis && series.xaxis.options.mode === 'log') {
            return NationalInstruments.HtmlVI.DataPipeline.decimate1DLog(series, start, end, buckets);
        }

        var startIndex = Math.min(Math.max(0, Math.floor(start)), data.length / 2);
        var endIndex = (end > 0) ? Math.min((data.length / 2), Math.floor(end + 2)) : 0;

        var nextBucket = function () {
            if (minIndex === maxIndex) {
                res[iindex] = minIndex;
                res[iindex + 1] = min;
                iindex += 2;
            } else {
                if (minIndex < maxIndex) {
                    res[iindex] = minIndex;
                    res[iindex + 1] = min;
                    res[iindex + 2] = maxIndex;
                    res[iindex + 3] = max;
                } else {
                    res[iindex] = maxIndex;
                    res[iindex + 1] = max;
                    res[iindex + 2] = minIndex;
                    res[iindex + 3] = min;
                }
                iindex += 4;
            }
        }

        var prepareBucket = function () {
            max = -Infinity;
            maxIndex = -1;
            min = Infinity;
            minIndex = -1;
        }

        length = endIndex - startIndex;

        if (buckets > length) {
            step = 1;
        }

        for (i = startIndex * 2; i < endIndex * 2; i += 2) {
            if (newBucket) {
                prepareBucket();
                newBucket = false;
            }

            if (data[i + 1] < min) {
                minIndex = data[i];
                min = data[i + 1];
            }
            if (data[i + 1] > max) {
                maxIndex = data[i];
                max = data[i + 1];
            }

            if (Math.floor(index) !== Math.floor(index + step)) {
                nextBucket();
                newBucket = true;
            }

            index += step;
        }

        if (!newBucket) {
            nextBucket();
        }

        return res;
    }

    NationalInstruments.HtmlVI.DataPipeline.decimatePoints = function (series, startX, endX, width, startY, endY, height) {
        if (height == null || endY == null || startY == null || width == null || endX == null || startX == null) {
            return series.datapoints.points;
        }

        if (startX > endX || startY > endY) {
            return [];
        }

        if ((series.xaxis && series.xaxis.options.mode === 'log') || (series.yaxis && series.yaxis.options.mode === 'log')) {
            return NationalInstruments.HtmlVI.DataPipeline.decimatePointsLog(series, startX, endX, width, startY, endY, height);
        }

        var verticalBucketStep = (endX - startX) / width,
            horizontalBucketStep = (endY - startY) / height,
            dataMatrix,
            data = series.datapoints.points,
            verticalBucketIndex, horizontalBucketIndex, i,
            res = [];

        dataMatrix = new Array(Math.ceil(width) * Math.ceil(height));

        for (i = 0; i < data.length; i += 2) {
            if (data[i] < startX || data[i + 1] < startY || data[i] > endX || data[i + 1] > endY) {
                continue;
            }
            verticalBucketIndex = data[i] === startX ? 0 : Math.ceil((data[i] - startX) / verticalBucketStep - 1);
            horizontalBucketIndex = data[i + 1] === startY ? 0 : Math.ceil((data[i + 1] - startY) / horizontalBucketStep - 1);
            updatePointsResult(data, i, verticalBucketIndex, horizontalBucketIndex, width, dataMatrix, res);
        }

        return res;
    }

    NationalInstruments.HtmlVI.DataPipeline.decimatePointsLog = function (series, startX, endX, width, startY, endY, height) {
        var dataMatrix,
            data = series.datapoints.points,
            verticalBucketIndex, horizontalBucketIndex, i,
            res = [],
            transformX = series.xaxis.options.transform || function (x) { return x; },
            transformY = series.yaxis.options.transform || function (y) { return y; },
            transformedStartX = transformX(startX),
            transformedStartY = transformY(startY),
            transformedEndX = transformX(endX),
            transformedEndY = transformY(endY),
            verticalBucketStep = (transformedEndX - transformedStartX) / width,
            horizontalBucketStep = (transformedEndY - transformedStartY) / height,
            transformedXValue, transformedYValue;

        dataMatrix = new Array(Math.ceil(width) * Math.ceil(height));

        for (i = 0; i < data.length; i += 2) {
            if (data[i] < startX || data[i + 1] < startY || data[i] > endX || data[i + 1] > endY) {
                continue;
            }
            transformedXValue = transformX(data[i]);
            transformedYValue = transformY(data[i + 1]);
            verticalBucketIndex = transformedXValue === transformedStartX ? 0 : Math.ceil((transformedXValue - transformedStartX) / verticalBucketStep - 1);
            horizontalBucketIndex = transformedYValue === transformedStartY ? 0 : Math.ceil((transformedYValue - transformedStartY) / horizontalBucketStep - 1);
            updatePointsResult(data, i, verticalBucketIndex, horizontalBucketIndex, width, dataMatrix, res);
        }

        return res;
    }

    function updatePointsResult(data, i, verticalBucketIndex, horizontalBucketIndex, width, dataMatrix, res) {
        var matrixIndex = verticalBucketIndex + horizontalBucketIndex * width;
        if (!dataMatrix[matrixIndex]) {
            res.push(data[i])
            res.push(data[i + 1]);
            dataMatrix[matrixIndex] = true;
        }
    }

    // Make sure ArrayBuffer.isView is available
    if (typeof ArrayBuffer.isView !== 'function') {
        // is that you PhantomJS ?
        ArrayBuffer.isView = function (data) {
            var isViewToStrings = [
                // The TypedArray types
                '[object Int8Array]',
                '[object Uint8Array]',
                '[object Uint8ClampedArray]',
                '[object Int16Array]',
                '[object Uint16Array]',
                '[object Int32Array]',
                '[object Uint32Array]',
                '[object Float32Array]',
                '[object Float64Array]',
                // The DataView type
                '[object DataView]'
            ];

            var dataStr = Object.prototype.toString.call(data);

            return isViewToStrings.indexOf(dataStr) !== -1;
        }
    }

    var DataTypesEnum = Object.freeze({
        niAnalogWaveform: 'niAnalogWaveform',
        niAnalogWaveformArray: 'niAnalogWaveformArray',
        XYClusterOfArrays: 'XYClusterOfArrays',
        XYArrayOfClusters: 'XYArrayOfClusters',
        XYArrayOfClusterOfArrays: 'XYArrayOfClusterOfArrays',
        XYArrayOfArrayOfClusters: 'XYArrayOfArrayOfClusters',
        oneDimensionNumericArray: '1DNumericArray',
        twoDimensionsNumericArray: '2DNumericArray',
        oneDimensionComplexArray: '1DComplexArray',
        twoDimensionsComplexArray: '2DComplexArray',
        numeric: 'numeric',
        clusterOfNumerics: 'clusterOfNumerics',
        arrayOfClusterOfNumerics: 'arrayOfClusterOfNumerics',
        historyBuffer: 'HistoryBuffer',
        empty: 'empty',
        unknown: 'unknown'
    });
    NationalInstruments.HtmlVI.DataPipeline.DataTypesEnum = DataTypesEnum;

    NationalInstruments.HtmlVI.DataPipeline.inferDataTypeFromNIType = function(niType) {
        if (!(niType instanceof NIType)) {
            return DataTypesEnum.unknown;
        }
        if (niType.getName() === NITypeNames.ANALOGWAVEFORM) {
            return DataTypesEnum.niAnalogWaveform;
        }
        if (niType.getName() === NITypeNames.ARRAY && niType.getSubtype().getName() === NITypeNames.ANALOGWAVEFORM) {
            return DataTypesEnum.niAnalogWaveformArray;
        }
        if (isXYClusterOfArrays(niType)) {
            return DataTypesEnum.XYClusterOfArrays;
        }
        if (niType.getName() === NITypeNames.ARRAY && niType.getRank() === 1 && isXYCluster(niType.getSubtype())) {
            return DataTypesEnum.XYArrayOfClusters;
        }
        if (niType.getName() === NITypeNames.ARRAY && isXYClusterOfArrays(niType.getSubtype())) {
            return DataTypesEnum.XYArrayOfClusterOfArrays;
        }
        if (niType.getName() === NITypeNames.ARRAY && niType.getRank() === 2 && isXYCluster(niType.getSubtype())) {
            return DataTypesEnum.XYArrayOfArrayOfClusters;
        }
        if (niType.getName() === NITypeNames.ARRAY && niType.getRank() === 1 && (niType.getSubtype().isInteger() || niType.getSubtype().isFloat())) {
            return DataTypesEnum.oneDimensionNumericArray;
        }
        if (niType.getName() === NITypeNames.ARRAY && niType.getRank() === 2 && (niType.getSubtype().isInteger() || niType.getSubtype().isFloat())) {
            return DataTypesEnum.twoDimensionsNumericArray;
        }
        if (niType.getName() === NITypeNames.ARRAY && niType.getRank() === 1 && niType.getSubtype().isComplex()) {
            return DataTypesEnum.oneDimensionComplexArray;
        }
        if (niType.getName() === NITypeNames.ARRAY && niType.getRank() === 2 && niType.getSubtype().isComplex()) {
            return DataTypesEnum.twoDimensionsComplexArray;
        }
        if (niType.isInteger() || niType.isFloat()) {
            return DataTypesEnum.numeric;
        }
        if (isClusterOfNumerics(niType)) {
            return DataTypesEnum.clusterOfNumerics;
        }
        if (niType.getName() === NITypeNames.ARRAY && isClusterOfNumerics(niType.getSubtype())) {
            return DataTypesEnum.arrayOfClusterOfNumerics;
        }
        return DataTypesEnum.unknown;
    }

    function isXYClusterOfArrays(niType) {
        return niType.getName() === NITypeNames.CLUSTER &&
            niType.getFields().length === 2 &&
            niType.getSubtype().length === 2 &&
            niType.getSubtype()[0].getName() === NITypeNames.ARRAY &&
            niType.getSubtype()[1].getName() === NITypeNames.ARRAY;
    }

    function isXYCluster(niType) {
        return niType.getName() === NITypeNames.CLUSTER &&
            niType.getFields().length === 2 &&
            niType.getSubtype().length === 2 &&
            (niType.getSubtype()[0].isInteger() || niType.getSubtype()[0].isFloat()) &&
            (niType.getSubtype()[1].isInteger() || niType.getSubtype()[1].isFloat());
    }

    function isClusterOfNumerics(niType) {
        return niType.getName() === NITypeNames.CLUSTER &&
            niType.getSubtype().every(function(st) {
                return st.isInteger() || st.isFloat();
            });
    }
}());
